
import BuildingCard from '@/components/minecraft/BuildingCard';
import { getHistoricalWorksGroupedByCompetition, getUserSession } from '@/app/actions';
import type { HistoricalCompetitionWithWorks } from '@/app/actions';
import { Separator } from '@/components/ui/separator';
import { ScrollText, CalendarDays, Trophy } from 'lucide-react';

export const dynamic = 'force-dynamic';

export default async function HistoryPage() {
  const competitionsWithWorks: HistoricalCompetitionWithWorks[] = await getHistoricalWorksGroupedByCompetition();
  const currentUser = await getUserSession();

  return (
    <div className="space-y-10">
      <div className="text-center space-y-3">
        <ScrollText className="mx-auto h-12 w-12 text-primary mb-2" />
        <h1 className="text-4xl font-extrabold tracking-tight text-primary">历史活动作品回顾</h1>
        <p className="text-xl text-muted-foreground">
          重温往届活动的精彩建筑作品，按活动分类展示。
        </p>
      </div>
      
      {competitionsWithWorks.length > 0 ? (
        competitionsWithWorks.map((competition, index) => (
          <section key={competition.id} aria-labelledby={`competition-title-${competition.id}`}>
            {index > 0 && <Separator className="my-10" />} {/* Separator between competitions */}
            <div className="mb-6 p-4 bg-card/50 rounded-lg shadow">
              <div className="flex items-center mb-2">
                <Trophy className="h-7 w-7 text-accent mr-3" />
                <h2 id={`competition-title-${competition.id}`} className="text-3xl font-bold text-primary">
                  {competition.name}
                </h2>
              </div>
              <div className="flex items-center text-muted-foreground mb-1">
                <CalendarDays className="h-4 w-4 mr-1.5" />
                <span>{competition.year}年</span>
                <span className="mx-2">|</span>
                <span>状态: {competition.status}</span>
              </div>
              {competition.description && (
                <p className="text-sm text-muted-foreground mt-1 italic">
                  {competition.description}
                </p>
              )}
            </div>
            
            {competition.works.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {competition.works.map((building) => (
                  <BuildingCard key={building.id} building={building} currentUser={currentUser} />
                ))}
              </div>
            ) : (
              <p className="text-center text-muted-foreground text-md py-6 bg-card/30 rounded-md">
                此活动下暂无作品记录。
              </p>
            )}
          </section>
        ))
      ) : (
        <div className="text-center text-muted-foreground text-lg py-10 bg-card/30 rounded-md mt-6">
          <p>暂无历史活动作品记录。</p>
          <p className="text-sm mt-2">也许是所有活动都在“进行中”？</p>
        </div>
      )}
    </div>
  );
}


    